.main-nav {
  .list-reset;
  display: block;
  width: @mobile-screen;

  @media (min-width: @tablet-screen) {
    display: flex;
    justify-content: space-between;
    width: 440px;
  }

  @media (min-width: @desktop-screen) {
    width: 480px;
  }
}

.main-nav__item {
  .margin-reset;
  padding: 22px 0 20px 0;
  border-top: 1px solid @divider-3;
  text-align: center;

  @media (min-width: @tablet-screen) {
    padding: 18px 0 0 0;
    border: none;
  }

  @media (min-width: @desktop-screen) {
    padding: 16px 0 0 0;
  }

  &:last-child {
    border-bottom: 1px solid @divider-3;

    @media (min-width: @tablet-screen) {
      border: none;
    }
  }
}

.main-nav__link {
  .link-normalize;
  .set-special-font;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: @basic-black;

  @media (min-width: @tablet-screen) {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }

  @media (min-width: @desktop-screen) {
    font-size: 20px;
    line-height: 30px;
    color: @basic-white;
  }
}
